___CSS_LOADER_EXPORT___.push([module.id, `.card {
  font-family: "Helvetica Neue", Arial, sans-serif;
}

* {
  box-sizing: border-box;
}

.history-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Chart.js Card, credits: Russell Palmiter, https://codepen.io/rozklad/pen/OOMGza */
.card {
  /* background: linear-gradient(-45deg, #5555ff, #9787ff); */
  /* background: linear-gradient(-45deg, rgba(85, 85, 255, 0.3), rgba(151, 135, 255, 0.3)); */
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
  position: relative;
  /* top: 50%; */
  /* left: 50%; */
  /* transform: translate(-50%, -50%); */
  width: 1250px;
  height: 375px;
  border-radius: 10px;
  overflow: hidden;
  display: flex;

  flex-direction: column; /* Use flex to arrange children vertically */
  align-items: center; /* Center content horizontally */
  justify-content: center; /* Evenly space out content vertically */
}

/* hide limit values on X axis */
.card #canvas {
  /* height: 75%; */
  /* margin-left: -30px;
    margin-right: -30px;    
    width: 112% !important; */
  width: 100% !important; /* Ensure the canvas fills the width */
  height: 50% !important; /* Ensure the canvas fills the height */
  padding: 10px;
  margin-bottom: 8%;
  font-weight: bold;
}

.card .about {
  height: 150px;
  padding: 20px;
  box-sizing: border-box;
}

.card .about h3,
.card .about .lead {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 400;
}

.card .about h3 {
  font-size: 24px;
  color: #fff;
}

.card .about .lead {
  color: #eee;
}

.card .info {
  float: left;
  padding: 10px 30px 10px 0;
}

.card .info p {
  font-size: 11px;
  color: #aaa;
  font-weight: 300;
}

.legends {
  padding-top: 20px;
  overflow: hidden;
}

.legend {
  display: block;
  width: 8px;
  height: 8px;
  margin-top: 15px;
  margin-bottom: 15px;
  border-radius: 50%;
}

.legend--this {
  background-color: #5555ff;
}

.legend--prev {
  background-color: #ff55b8;
}

.axis {
  position: absolute;
  color: #fff;
  z-index: 1;
  text-transform: uppercase;
  display: flex;
  width: 100%;
  bottom: 0;
  font-weight: bold;
}

.axis .tick img {
  height: 40px;
  width: auto;
}
.axis .tick {
  flex: 1;
  position: relative;
  font-size: 16px;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  line-height: 25px;
}

.axis .tick::after {
  content: "";
  position: absolute;
  display: block;
  right: 0;
  bottom: 0;
  width: 1px;
  height: 200px;
  background: rgba(255, 255, 255, 0.2);
}

.axis .tick .value {
  transform: translateY(-240px);
  opacity: 0;
  transition: all 0.3s;
  position: absolute;
  top: 20px;
  left: 0;
  color: #fff;
  border-radius: 2px;
  width: 100%;
  line-height: 20px;
}

.axis .tick:hover .value.value--this {
  transform: translateY(-180px);
  display: block;
  opacity: 1;
  font-weight: bolder;
}

.value.value--this {
  color: #fff;
  font-weight: bold;
  font-size: 20px;
}

.day-number {
  display: block;
}

.day-name {
  display: block;
  /* opacity: 0.4; */
}

/* Animated background, credits: Manuel Pinto, https://codepen.io/P1N2O/pen/pyBNzX */
.card {
  /* background: linear-gradient(-45deg, #555ff, #9787ff, #ff55b8, #ff8787); */
  /* Space Theme */
  /* background: linear-gradient(-45deg, rgba(55, 105, 255, 0.4), rgba(150, 50, 200, 0.3), rgba(30, 200, 120, 0.2), rgba(0, 180, 200, 0.35)); */
  /* Background Colors */
  background: linear-gradient(-45deg, rgba(36, 60, 64, 0.4), rgba(209, 208, 191, 0.3), rgba(131, 166, 156, 0.2), rgba(112, 147, 156, 0.35));
  /* Complentary Colors */
  /* background: linear-gradient(-45deg, rgba(64, 40, 36, 0.4), rgba(187, 252, 13, 0.3), rgba(166, 131, 141, 0.2), rgba(156, 121, 112, 0.35)); */

  background-size: 400% 400%;
  animation: bg 20s infinite;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.1); /* Optional: Add a shadow for depth */
}

@keyframes bg {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
